import * as React from 'react';
import { DateTime } from 'luxon';
import styled from '@emotion/styled';
import { Todos } from './Todos';

export function Clock() {
  const [time, setTime] = React.useState(DateTime.now());

  React.useEffect(() => {
    const timerId = setInterval(() => setTime(DateTime.now()), 1000);

    return () => clearInterval(timerId);
  }, []);

  return (
    <Root>
      <Date>
        <div>{time.toFormat("cccc',' LLLL d")}</div>
        <Week>{`Week ${time.toFormat('W')}`}</Week>
      </Date>
      <Time>{time.toLocaleString(DateTime.TIME_SIMPLE)}</Time>
      <Todos />
    </Root>
  );
}

const Root = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 300px;
  max-width: 450px;
  padding: 24px;
  gap: 24px;
  border-radius: 12px;
  background: #fae5f7;
  color: ${({ theme }) => theme.colors.ui.regular};
`;

const Date = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-weight: 600px;
  font-size: 14px;
`;

const Week = styled.div`
  letter-spacing: 0.04em;
  text-transform: uppercase;
  font-size: 11px;
`;

const Time = styled.div`
  font-family: GT Haptik, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
`;
