import * as React from 'react';
import { useTheme } from '@emotion/react';
import styled from '@emotion/styled';
import { FormattedMessage, defineMessages } from 'react-intl';
import { CrossIcon } from '@src/ui/icons/CrossIcon';

const messages = defineMessages({
  surveyMessage: {
    id: 'newTab.surveyMessage',
    defaultMessage: `Help us improve this page. Take a quick survey.`,
  },
});

export function SurveyBanner({ onClose }: { onClose: () => void }) {
  const theme = useTheme();

  return (
    <Root
      href='https://toggl.typeform.com/to/ozhpbHyC'
      target='_blank'
      rel='noreferrer'
    >
      <FormattedMessage {...messages.surveyMessage} />
      <CrossIcon
        color={theme.colors.ui_50.regular}
        onClick={e => {
          e.preventDefault();
          onClose();
        }}
      />
    </Root>
  );
}

const Root = styled.a`
  display: flex;
  padding: 12px;
  justify-content: center;
  align-items: center;
  gap: 8px;

  border-radius: 22px;
  background-color: ${({ theme }) => theme.colors.ui_90.regular};
  color: #fde5dc;

  font-size: 14px;
  text-decoration: none;
`;
