import * as React from 'react';
import styled from '@emotion/styled';
import { AddIcon } from '@src/ui/icons/AddIcon';
import { useTheme } from '@emotion/react';
import { Db } from '@src/lib/db';
import { NewTabOnboardingTasks } from '@src/lib/constants';
import ContinueIcon from '@src/ui/icons/ContinueIcon';
import { TimeEntryRequest, TimeEntryV9 } from '@toggl/track-types';
import { useExtensionContext } from '@src/context';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import * as timeEntryService from '@src/services/timeEntry';
import * as errorReport from '@src/lib/errorReport';
import { Checkbox } from '@src/ui/components/Checkbox';
import { DateTime } from 'luxon';
import { scrollbarStyle } from '../styles';
import { track } from '@src/lib/utils';

type Todo = {
  id: number;
  text: string;
  complete: boolean;
  created_at: string;
};

export function Todos() {
  const [todos, setTodos] = React.useState<{ [key: number]: Todo }>({});
  const queryClient = useQueryClient();
  const { user } = useExtensionContext();

  const { mutate: startEntry } = useMutation(
    (entry: TimeEntryRequest) =>
      timeEntryService.start(entry, { type: 'popup' }),
    {
      onMutate: async entry => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => {
          const newEntries: Record<number, TimeEntryV9> = {};
          if (old) {
            Object.assign(newEntries, old);
          }
          newEntries[-1] = { ...entry, id: -1 } as TimeEntryV9;
          return newEntries;
        });
        return { previousEntries };
      },
      onError: (err, _, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: (entry, _, context) => {
        track('start-time-entry', { context: 'ntp-goal' });
        const entries = {
          ...context?.previousEntries,
          [entry.id]: entry,
        };
        delete entries[-1];
        timeEntryService.setQueryData(queryClient, entries);
      },
    }
  );

  const startTimer = (e, description) => {
    e.preventDefault();
    if (!user) return;
    const start = new Date();

    const entry = {
      at: start.toISOString(),
      description,
      duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
      start: start.toISOString(),
      tags: [],
      tag_ids: [],
      workspace_id: user.default_workspace_id,
    } as unknown as TimeEntryRequest;

    startEntry(entry);
  };

  React.useEffect(() => {
    const loadTodos = async () => {
      const onboarded = await Db.get('newTabsPageOnboarded');
      if (onboarded) {
        const loadedTodos = await Db.getLocal('newTab-todos');
        const parsedTodos: Record<number, Todo> = JSON.parse(loadedTodos);
        const filteredTodos = Object.values(parsedTodos).reduce((acc, todo) => {
          const todoDate = DateTime.fromISO(todo.created_at);
          const today = DateTime.now();

          if (today.diff(todoDate, 'days').days < 1 || !todo.complete)
            acc[todo.id] = todo;
          return acc;
        }, {});

        setTodos(filteredTodos);
      } else {
        void Db.setLocal('newTab-todos', JSON.stringify(NewTabOnboardingTasks));
        void Db.set('newTabsPageOnboarded', true);
        setTodos(NewTabOnboardingTasks);
      }
    };

    loadTodos();
  }, []);

  React.useEffect(() => {
    if (Object.keys(todos).length > 0)
      Db.setLocal('newTab-todos', JSON.stringify(todos));
  }, [todos]);

  const addTodo = (text: string) =>
    setTodos(oldTodos => {
      const todosIds = Object.keys(oldTodos);
      const id = todosIds.length
        ? Number(todosIds.sort()[todosIds.length - 1]) + 1
        : 1;

      return {
        ...oldTodos,
        [id]: {
          id,
          text,
          complete: false,
          created_at: DateTime.now().toISO(),
        },
      };
    });

  const toggleTodo = (index: number) => {
    !todos[index].complete && track('ntp-complete-goal');
    setTodos(oldTodos => ({
      ...oldTodos,
      [index]: {
        ...oldTodos[index],
        complete: !oldTodos[index].complete,
      },
    }));
  };

  return (
    <Root>
      <AddTodo onAddTodo={addTodo} />
      <ul>
        {Object.values(todos)
          .sort(a => (a.complete ? 1 : -1))
          .map((todo: Todo) => (
            <li key={`todo-${todo.id}`}>
              <Checkbox
                checked={todo.complete}
                name={`todo-${todo.id}`}
                onClick={() => toggleTodo(todo.id)}
              />
              <label htmlFor={`todo-${todo.id}`}>{todo.text}</label>
              <ContinueIcon
                title='Track task'
                onClick={e => startTimer(e, todo.text)}
              />
            </li>
          ))}
      </ul>
    </Root>
  );
}

function AddTodo({ onAddTodo }: { onAddTodo: (text: string) => void }) {
  const [text, setText] = React.useState('');
  const theme = useTheme();

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    track('ntp-add-goal');

    onAddTodo(text);
    setText('');
  };

  return (
    <Wrapper onSubmit={handleSubmit}>
      <AddIcon color={theme.colors.ui_50.regular} />
      <TextInput
        type='text'
        placeholder='Add a goal for today'
        value={text}
        onChange={e => setText(e.target.value)}
      />
    </Wrapper>
  );
}

const Root = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 24px;
  font-size: 14px;
  color: ${({ theme }) => theme.colors.ui.regular};

  ul {
    list-style: none;
    padding: 0;
    max-height: 40vh;
    overflow-y: auto;

    ${({ theme }) => scrollbarStyle(theme)};
  }

  li {
    display: flex;
    padding: 12px 4px;
    gap: 8px;
    cursor: pointer;
    align-items: center;

    input:checked + label {
      text-decoration: line-through;
    }

    svg {
      display: none;
    }

    input ~ svg {
      display: flex;
    }

    &:hover {
      svg {
        display: flex;
      }
    }
  }
`;

const Wrapper = styled.form`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 36px;
  padding: 0 15px;
  gap: 4px;
  border: 2px solid ${({ theme }) => theme.colors.ui_40.regular};
  border-radius: 8px;
  color: ${({ theme }) => theme.colors.ui_60.regular};
`;

const TextInput = styled.input`
  border: none;
  background: none;
  outline: none;
`;
