import * as React from 'react';
import styled from '@emotion/styled';
import Logo from '@ui/icons/Logo';
import { css, useTheme } from '@emotion/react';
import * as userService from '@src/services/user';

import { ExtensionContext, useContextState } from '@src/context';
import { TimerWidget } from './widgets/TimerWidget';
import { Clock } from './widgets/Clock';
import { RecentlyTracked } from './widgets/RecentlyTracked';
import { CalendarEvents } from './widgets/CalendarEvents';
import { Links } from './widgets/Links';
import Background from './Background';
import { Greeting } from './components/Greeting';
import { LoginWidget } from './widgets/LoginWidget';
import { SurveyBanner } from './components/SurveyBanner';
import { SpinningLoadingIcon } from '@src/ui/icons/LoadingIcon';
import '@src/lib/heap';
import useSetting from '@src/lib/hooks/useSetting';

export const NewTab = () => {
  const theme = useTheme();
  const { data: user, refetch, isFetching, isLoading } = userService.useUser();
  const state = useContextState();
  const [loading, setLoading] = React.useState(true);
  const [isSurveyShown, setSurveyShown] = useSetting('surveyNTPTaken');

  React.useEffect(() => {
    refetch();
  }, []);

  React.useEffect(() => {
    // React query loading states returns false on the first render...
    if (!isFetching && !isLoading) setLoading(false);
  }, [isLoading, isFetching]);

  return (
    <ExtensionContext.Provider value={state}>
      <Root>
        <FullpageBackground />
        {!loading ? (
          <>
            <Links />
            <Content>
              <div
                css={css`
                  grid-column: span 3;
                `}
              >
                <Greeting username={user?.fullname} />
              </div>
              {!user ? (
                <Widget
                  css={css`
                    grid-column: span 3;
                  `}
                >
                  <LoginWidget />
                </Widget>
              ) : (
                <>
                  <Widget
                    css={css`
                      grid-row: 2 / span 2;
                    `}
                  >
                    <Clock />
                  </Widget>
                  <Widget
                    css={css`
                      grid-column: 2 / span 2;
                      width: 100%;

                      & > div > div,
                      input#timerDescription {
                        background-color: #fdece5;
                        box-shadow: none;
                      }
                    `}
                  >
                    <TimerWidget />
                  </Widget>
                  <Widget
                    css={css`
                      grid-row: 3 / span 2;
                    `}
                  >
                    <RecentlyTracked />
                  </Widget>
                  <Widget
                    css={css`
                      grid-row: 3 / span 2;
                    `}
                  >
                    <CalendarEvents />
                  </Widget>
                </>
              )}
            </Content>
          </>
        ) : (
          <LoadingContainer>
            <SpinningLoadingIcon size={64} color={theme.colors.ui_50.regular} />
          </LoadingContainer>
        )}
        {!isSurveyShown && (
          <SurveyContainer>
            <SurveyBanner onClose={() => setSurveyShown(true)} />
          </SurveyContainer>
        )}

        <Logo
          css={css`
            position: absolute;
            bottom: 32px;
            left: 32px;
          `}
        />
      </Root>
    </ExtensionContext.Provider>
  );
};

const Root = styled.div`
  width: 100vw;
  height: 100vh;
  position: relative;
  padding: 20px 40px;
  color: ${({ theme }) => theme.colors.ui.regular};
  display: flex;
  justify-content: center;
  flex-direction: column;
`;

const Content = styled.div`
  display: grid;
  grid-template-columns: 350px 350px minmax(350px, 500px);
  grid-template-rows: 72px 130px 200px 1fr;
  gap: 32px;
  overflow: auto;
  width: 100%;
  z-index: 10;
  max-width: 1240px;
  height: 100%;
  padding: 0px 40px;

  @media (min-width: 1460px) {
    align-self: center;
    width: auto;
  }
`;

const Widget = styled.div`
  height: fit-content;
`;

const FullpageBackground = styled(Background)`
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
`;

const LoadingContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: ${({ theme }) => theme.colors.surface_primary.regular};
`;

const SurveyContainer = styled.div`
  position: absolute;
  bottom: 32px;
  left: calc(50% - 200px);
  z-index: 99999;
`;
