import { useQuery } from '@tanstack/react-query';

import { sendMessage } from '@src/lib/messaging';

const fetchTodayEvents = () =>
  sendMessage({
    type: 'today-calendar-events',
    payload: null,
  });

const fetchCalendarIntegrations = () =>
  sendMessage({
    type: 'fetch-calendars',
    payload: null,
  });

export const useTodayCalendarEvents = () => {
  return useQuery(['todayCalendarEvents'], fetchTodayEvents, {
    staleTime: 0,
  });
};

export const useCalendarIntegrations = () => {
  return useQuery(['calendarIntegrations'], fetchCalendarIntegrations, {
    staleTime: 0,
  });
};
