import styled from '@emotion/styled';

import { useExtensionContext } from '@src/context';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import {
  Client,
  Project,
  TimeEntryRequest,
  TimeEntryV9,
  Workspace,
} from '@toggl/track-types';
import { hslToColorString, parseToHsl } from 'polished';
import * as React from 'react';
import { FormattedMessage, defineMessages } from 'react-intl';
import * as timeEntryService from '@src/services/timeEntry';
import * as projectService from '@src/services/project';
import * as errorReport from '@src/lib/errorReport';
import ContinueIcon from '@src/ui/icons/ContinueIcon';
import { WidgetDescription } from '../styles';
import { track } from '@src/lib/utils';
import { TETooltip } from '@src/components/TETooltip';

const messages = defineMessages({
  title: {
    id: 'newTab.recentlyTracked.title',
    defaultMessage: 'Recently Tracked',
  },

  placeholder: {
    id: 'newTab.recentlyTracked.placeholder',
    defaultMessage:
      'No time entries yet.{br} Start the timer above â˜ï¸ and create {br} your first one!',
  },
});

const MOUSEOVER_TIMEOUT = 500;

const isPropValuesEqual = (subject, target, propNames) =>
  propNames.every(
    propName =>
      `${subject[propName]}`.toLowerCase() ===
      `${target[propName]}`.toLowerCase()
  );

const getUniqueItemsByProperties = (items, propNames) =>
  items.filter(
    (item, index, array) =>
      index ===
      array.findIndex(foundItem =>
        isPropValuesEqual(foundItem, item, propNames)
      )
  );

export const RecentlyTracked = () => {
  const { timeEntries, workspaces, user, refetchEntries } =
    useExtensionContext();

  const [timeEntriesToShow, setTimeEntriesToShow] = React.useState<
    TimeEntryV9[]
  >([]);
  const queryClient = useQueryClient();

  const { data: projects } = projectService.useProjects();
  const { data: tasks } = projectService.useTasks();
  const { data: clients } = projectService.useClients();

  const { mutate: startEntry } = useMutation(
    (entry: TimeEntryRequest) =>
      timeEntryService.start(entry, { type: 'popup' }),
    {
      onMutate: async entry => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => {
          const newEntries: Record<number, TimeEntryV9> = {};
          if (old) {
            Object.assign(newEntries, old);
          }
          newEntries[-1] = { ...entry, id: -1 } as TimeEntryV9;
          return newEntries;
        });
        return { previousEntries };
      },
      onError: (err, _, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: (entry, _, context) => {
        track('start-time-entry', { context: 'ntp-history' });
        const entries = {
          ...context?.previousEntries,
          [entry.id]: entry,
        };
        delete entries[-1];
        timeEntryService.setQueryData(queryClient, entries);
      },
    }
  );

  const startTimer = (e, te?) => {
    e.preventDefault();
    if (!user) return;
    const start = new Date();

    const entry = {
      at: start.toISOString(),
      billable: te.billable,
      description: te.description,
      duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
      start: start.toISOString(),
      tags: te.tags ?? [],
      tag_ids: te.tag_ids ?? [],
      task_id: te.task_id,
      project_id: te.project_id,
      workspace_id: te.workspace_id,
    } as unknown as TimeEntryRequest;

    startEntry(entry);
  };

  React.useEffect(() => {
    if (timeEntries) {
      const withDescription = Object.values(timeEntries).filter(
        te => (te.project_id || te.description) && te.stop
      );
      const filteredTimeEntries = withDescription;
      const uniqTimeEntries = getUniqueItemsByProperties(filteredTimeEntries, [
        'description',
      ]);
      const timeEntriesWithoutArchivedProjects = uniqTimeEntries.filter(te => ({
        ...te,
        project_id: projects?.[`${te.project_id}`]?.active
          ? te.project_id
          : null,
      }));

      setTimeEntriesToShow(
        timeEntriesWithoutArchivedProjects.reverse().slice(0, 5)
      );
    }
  }, [clients, projects, tasks, timeEntries]);

  React.useEffect(() => {
    refetchEntries();
  }, [refetchEntries]);

  const handleTimeEntryClick = (timeEntry: TimeEntryV9, e) => {
    startTimer(e, timeEntry);
  };

  if (!timeEntriesToShow?.length)
    return (
      <EmptyStateRoot>
        <FormattedMessage
          {...messages.placeholder}
          tagName={WidgetDescription}
          values={{ br: <br /> }}
        />
      </EmptyStateRoot>
    );

  return (
    <Root>
      <FormattedMessage {...messages.title} tagName={Title} />
      {!!timeEntriesToShow?.length && (
        <>
          {timeEntriesToShow.map(te => (
            <AutocompleteItem
              key={`te-${te.id}`}
              description={te.description}
              project={projects?.[`${te.project_id}`]}
              workspace={workspaces?.[`${te.workspace_id}`]}
              onClick={e => handleTimeEntryClick(te, e)}
              client={clients?.[`${projects?.[`${te.project_id}`]?.client_id}`]}
            />
          ))}
        </>
      )}
    </Root>
  );
};

type AutocompleteItemProps = {
  description?: string;
  project?: Project;
  client?: Client;
  showClient?: boolean;
  workspace?: Workspace;
  onClick: (e: React.MouseEvent<HTMLLIElement>) => void;
};

const AutocompleteItem = React.forwardRef<any, AutocompleteItemProps>(
  function AutocompleteItem({
    description,
    project,
    client,
    showClient = false,
    workspace,
    onClick,
  }) {
    const [isHovered, setIsHovered] = React.useState(false);
    const elementRef = React.useRef<HTMLLIElement>(null);

    let closureTimeout: NodeJS.Timeout | undefined;

    const handleMouseOver = () => {
      if (closureTimeout) {
        clearTimeout(closureTimeout);
      }
      closureTimeout = setTimeout(() => setIsHovered(true), MOUSEOVER_TIMEOUT);
    };

    const handleMouseLeave = () => {
      if (closureTimeout) {
        clearTimeout(closureTimeout);
      }
      closureTimeout = setTimeout(
        () => setIsHovered(false),
        MOUSEOVER_TIMEOUT / 2
      );
    };

    return (
      <ItemsContainer>
        <Item
          onClick={onClick}
          onMouseOver={handleMouseOver}
          onMouseLeave={handleMouseLeave}
          ref={elementRef}
          bgColor={(project && project.color) ?? undefined}
        >
          {description && (
            <DescriptionLabel tooltip={false}>{description}</DescriptionLabel>
          )}
          {project && (
            <ProjectLabel color={project.color} tooltip={false}>
              {project.name}
            </ProjectLabel>
          )}
          {client && showClient && (
            <ClientLabel tooltip={false}>{client.name}</ClientLabel>
          )}
          <ContinueIcon title='Continue entry' />
        </Item>
        {isHovered && (
          <StyledTETooltip
            description={description}
            project={project}
            client={client}
            workspace={workspace}
            positionRef={elementRef}
          />
        )}
      </ItemsContainer>
    );
  }
);

