import * as React from 'react';
import styled from '@emotion/styled';
import { DateTime } from 'luxon';

import { FormattedMessage, defineMessages } from 'react-intl';

type Props = {
  username: string | undefined;
};

const messages = defineMessages({
  morningGreeting: {
    id: 'newTab.morningGreeting',
    defaultMessage: `Good morning, <em>{name}</em>`,
  },
  afternoonGreeting: {
    id: 'newTab.afternoonGreeting',
    defaultMessage: `Good afternoon, <em>{name}</em>`,
  },
  eveningGreeting: {
    id: 'newTab.eveningGreeting',
    defaultMessage: `Good evening, <em>{name}</em>`,
  },
  nightGreeting: {
    id: 'newTab.nightGreeting',
    defaultMessage: `Good night, <em>{name}</em>`,
  },
});

const getTimeOfDay = (now: DateTime) => {
  const hours = now.get('hour');

  switch (true) {
    case hours >= 5 && hours < 12:
      return messages.morningGreeting;
    case hours >= 12 && hours < 18:
      return messages.afternoonGreeting;
    case hours >= 18 && hours < 22:
      return messages.eveningGreeting;
    case hours >= 22 && hours < 5:
      return messages.nightGreeting;
    default:
      return messages.nightGreeting;
  }
};

export function Greeting({ username }: Props) {
  const greeting = getTimeOfDay(DateTime.now());

  return (
    <Root>
      <FormattedMessage
        {...greeting}
        values={{
          em: chunks => <em>{chunks}</em>,
          name: username ?? 'Stranger',
        }}
      />
    </Root>
  );
}

const Root = styled.div`
  font-family: GT Haptik, sans-serif;
  font-weight: 700;
  font-size: 45px;
  line-height: 160%;
  color: ${({ theme }) => theme.colors.pale_peach.regular};

  em {
    font-family: GT Haptik Rotalic, sans-serif;
    font-style: none;
  }
`;
